window.slideDetails = {
    getData(id) {
        // console.log(id);
        let data = portfolio.getData(id);

        let slideDetailsEl = document.querySelector('.slide-details');

        let tags = '';

        // console.log(data);

        if (data.tags.length) {
            tags += `<div class="tags">`;

            data.tags.forEach(tag => {
                tags += `<div class="tag">${tag}</div>`;
            });

            tags += `</div>`;
        }

        let sliderImages = '';

        if (data.images.length) {
            sliderImages += `<div class="details__slider">
            <div class="splide">
                <div class="splide__arrows">
                    <button class="splide__arrow splide__arrow--prev">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fill-rule="evenodd" d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z" clip-rule="evenodd" />
                        </svg>
                    </button>
                    <button class="splide__arrow splide__arrow--next">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                            <path fill-rule="evenodd" d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z" clip-rule="evenodd" />
                        </svg>
                    </button>
                </div>`;

                sliderImages += `<div class="splide__track">
                                <ul class="splide__list">`;

                data.images.forEach(img => {
                    sliderImages += `<li class="splide__slide">
                        <a data-fslightbox href="${SITE_URL}/${img}" target="preview">
                            <img src="${SITE_URL}/${img}" alt="" />
                        </a>
                    </li>`;
                });

                sliderImages += `</ul>
                </div>
            </div>
            </div>`;
        }

        let output = `<div class="main">
            <div class="logo">
                <img src="${SITE_URL}/frontend/assets/images/logo-w-01.svg" width="50" height="50" />
            </div>

            <div class="category">${data.category}</div>

            <div class="title">${data.name}</div>

            ${tags}

            ${sliderImages}
        </div>

        <div class="scrollable">${data.content}</div>
        `;

        slideDetailsEl.querySelector('.content').innerHTML = output;

        slideDetails.init('.details__slider .splide');

        anime({
            targets: slideDetailsEl,
            opacity: [0, 1],
            scaleX: [1.5, 1],
            scaleY: [.1, 1],
            easing: 'easeInOutExpo',
            duration: 1000,
        }).finished.then(() => {
            slideDetailsEl.style.pointerEvents = 'all';
        });
    },

    init(selector) {
        let slider = new Splide(selector, {
            // type: 'loop',
            fixedWidth: 250,
            autoplay: true,
            gap: 10,
            pagination: true,
            arrows: false,
        });

        slider.mount();

        refreshFsLightbox();
    },

    close() {
        let slideDetailsEl = document.querySelector('.slide-details');
        anime({
            targets: slideDetailsEl,
            opacity: 0,
            scaleX: 1.5,
            scaleY: .1,
            easing: 'easeInOutExpo',
            duration: 500
        }).finished.then(() => {
            slideDetailsEl.style.pointerEvents = 'none';
        });
    }
};

