window.submitForm = {
    showError(form, errors) {
        if (form.querySelector('.validation-error')) {
            form.querySelectorAll('.validation-error').forEach(item => {
                item.remove();
            });
        }

        for (const [field, msg] of Object.entries(errors)) {
            let fieldItem = form.querySelector(`[name="${field}"]`);

            if (fieldItem) {
                fieldItem.insertAdjacentHTML('afterend', `<div class="validation-error small mt-1 text-danger">${msg}</div>`);
            }
        }
    },

    init(logResponse = true) {
        let apiforms = document.querySelectorAll('form.apiform');
        if (apiforms) {
            let api_url = "https://www.weblozy.com/basic/ilp/implementation/form-submit";

            if (typeof API_URL == 'string') {
                api_url = API_URL;
            }

            apiforms.forEach(form => {
                form.addEventListener('submit', e => {
                    e.preventDefault();

                    let form = e.target;

                    let spinner = `<svg class="spinner" viewBox="0 0 50 50">
                        <circle class="path" cx="25" cy="25" r="20" fill="none" stroke-width="5"></circle>
                    </svg>`;

                    form.insertAdjacentHTML('afterBegin', spinner);

                    let fdata = new FormData(form);

                    form.classList.add('waiting');

                    if (logResponse) {
                        console.log({
                            name: fdata.get('name'),
                            email: fdata.get('email'),
                            phone: fdata.get('phone'),
                            requirement: fdata.get('requirement'),
                            msg: fdata.get('msg'),
                            url: window.location.href
                        });
                    }

                    axios.post(api_url, {
                        name: fdata.get('name'),
                        email: fdata.get('email'),
                        phone: fdata.get('phone'),
                        requirement: fdata.get('requirement'),
                        msg: fdata.get('msg'),
                        url: window.location.href
                    })
                    .then(resp => {
                        if (logResponse) {
                            console.log(resp.data);
                        }

                        form.classList.remove('waiting');
                        form.querySelector('.spinner').remove();

                        let data = resp.data;

                        let output;
                        if (data.success) {
                            if (form.querySelector('.validation-error')) {
                                form.querySelectorAll('.validation-error').forEach(item => {
                                    item.remove();
                                });
                            }

                            output = '<div class="mt-2 text-success">Form submitted successfully.</div>';

                            setTimeout(() => {
                                let redirectTo = `${SITE_URL}/thank-you?name=${fdata.get('name')}`;

                                window.location.href = encodeURI(redirectTo);
                            }, 0);
                        }
                        else {
                            let errors = data.errors;
                            this.showError(form, errors);

                            output = '<div class="mt-2 text-danger">Form submission not successful.</div>';
                        }

                        form.querySelector('.status').innerHTML = output;
                    });
                });
            });
        }
    }
};

if (document.querySelector('.shadow-wrap')) {

    window.shadowMove = (pointerX, pointerY) => {
        let swrap = document.querySelector('.shadow-wrap');
        let sbody = swrap.querySelector('.shadow__body');
        let schild = swrap.querySelectorAll('.shadow__child');

        let sbodyRect = sbody.getBoundingClientRect();

        const sbodyMid = {
            x: sbodyRect.x + (sbodyRect.width / 2),
            y: sbodyRect.y + (sbodyRect.height / 2)
        };

        const pointer = {
            x: pointerX,
            y: pointerY
        };

        const p1 = sbodyMid;
        const p2 = pointer;

        let angleRad = Math.atan2(p1.y - p2.y, p1.x - p2.x);
        // let angleDeg = angleRad * 180 / Math.PI;

        schild.forEach((el, index) => {
            let pointerDx = pointer.x - sbodyMid.x;
            let pointerDy = pointer.y - sbodyMid.y;

            let offsetPointer = Math.hypot(pointerDx, pointerDy);

            // Multiply by -1 to get the opposite direction
            // let offset = 20;
            let offset = offsetPointer / 50;

            let offsetFinal = (index + 1) * offset;

            // let negate = (index > 2) ? 1 : -1;

            let dX = offsetFinal * Math.cos(angleRad) * -1;
            let dY = offsetFinal * Math.sin(angleRad) * -1;

            el.style.transform = `translate(${dX}px, ${dY}px)`;
        });
    }

    document.addEventListener('mousemove', e => {
        // console.log(e.clientX, e.clientY);
        shadowMove(e.clientX, e.clientY);
    }, {
        passive: true
    });

    // shadowMove(0, window.innerHeight/2);
}

document.addEventListener('mouseover', e => {

    // Closest Cursor Targets
    let cursorTargets = [
        'a[href]',
        'button',
        '.btn',
        '.to-top',
        '.accordion-toggle',
        '.accordion-head',
        '.fslightbox-slide-btn',
        '.fslightbox-toolbar-button',
        '.close',
        '.splide__arrow',
        '.details__slider .splide__slide',
        '.splide__pagination button',
        '.project__controls .arrow',
        '.video-wrapper .btn-play'
    ];

    if (e.target.closest(cursorTargets)) {
        document.querySelector('.cursor').classList.add('hover');

    }
    else {
        document.querySelector('.cursor').classList.remove('hover');
    }
});

