document.addEventListener('DOMContentLoaded', () => {
    if (document.querySelector('.section-blog-single .blog-content pre code')) {
        document.querySelectorAll('pre code').forEach((el) => {
          hljs.highlightElement(el);
        });
    }

    if (document.querySelector('#gtranslate')) {
        window.googleTranslateElementInit = () => {
            new google.translate.TranslateElement({
                pageLanguage: 'en',
                layout: google.translate.TranslateElement.InlineLayout.HORIZONTAL,
                autoDisplay: true,
            }, 'gtranslate');

            document.querySelector('#gtranslate .goog-te-gadget > div').nextSibling.remove();
            document.querySelector('#gtranslate .goog-te-gadget > span').remove();
            document.querySelector('#gtranslate select').classList.add('form-select', 'form-control');
        }
    }

    if (document.querySelector('#pftabcontent')) {
        let tabSliders = document.querySelectorAll('#pftabcontent .slider-pf');

        const sliders = {
            tabs: {},
        };

        let i = 0;

        tabSliders.forEach(slide => {
            sliders.tabs[`slide${i}`] = new Splide(slide, {
                type: 'slide',
                rewind: true,
                autoplay: true,
                arrows: false,
                pagination: true,
                gap: 0,
                focus: 'center',
                perPage: 1,
                perMove: 1,
                waitForTransition: true,
            }).mount();

            i++;
        });

        window.sliders = sliders;

        const tabEls = document.querySelectorAll('.section-pf button[data-bs-toggle="pill"]');

        console.log(tabEls);

        tabEls.forEach(tabEl => {
            tabEl.addEventListener('shown.bs.tab', event => {
                let index = Array.from(tabEls).indexOf(event.target);

                if (sliders.tabs[`slide${index}`]) {
                    let currentSlider = sliders.tabs[`slide${index}`];

                    currentSlider.emit('resize');

                    // console.log(currentSlider);
                }
            });
        });

        let viewBtns = document.querySelectorAll('.section-pf .pcard .btn-viewproject');

        viewBtns.forEach(btn => {
            btn.addEventListener('click', (e) => {
                e.preventDefault();

                let target = e.currentTarget;

                let slideDetailsEl = document.querySelector('.slide-details');

                let id = target.dataset.id;

                let data = pfdata[id];

                let tags = '';

                if (data.tag_array.length) {
                    tags += `<div class="tags">`;

                    data.tag_array.forEach(tag => {
                        tags += `<div class="tag">${tag}</div>`;
                    });

                    tags += `</div>`;
                }

                let sliderImages = '';

                if (data.media.length) {
                    sliderImages += `<div class="details__slider">
                    <div class="splide">
                        <div class="splide__arrows">
                            <button class="splide__arrow splide__arrow--prev">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                    <path fill-rule="evenodd" d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z" clip-rule="evenodd" />
                                </svg>
                            </button>
                            <button class="splide__arrow splide__arrow--next">
                                <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                    <path fill-rule="evenodd" d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z" clip-rule="evenodd" />
                                </svg>
                            </button>
                        </div>`;

                        sliderImages += `<div class="splide__track">
                                        <ul class="splide__list">`;

                        data.media.forEach((img, index) => {
                            if (index > 0) {
                                sliderImages += `<li class="splide__slide">
                                    <a data-fslightbox href="${img.original_url}" target="preview">
                                        <img src="${img.preview_url}" alt="" />
                                    </a>
                                </li>`;
                            }
                        });

                        sliderImages += `</ul>
                        </div>
                    </div>
                    </div>`;
                }

                let icon = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" width="12" height="12"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><path fill="currentColor" d="M384 320c-17.67 0-32 14.33-32 32v96H64V160h96c17.67 0 32-14.32 32-32s-14.33-32-32-32L64 96c-35.35 0-64 28.65-64 64V448c0 35.34 28.65 64 64 64h288c35.35 0 64-28.66 64-64v-96C416 334.3 401.7 320 384 320zM488 0H352c-12.94 0-24.62 7.797-29.56 19.75c-4.969 11.97-2.219 25.72 6.938 34.88L370.8 96L169.4 297.4c-12.5 12.5-12.5 32.75 0 45.25C175.6 348.9 183.8 352 192 352s16.38-3.125 22.62-9.375L416 141.3l41.38 41.38c9.156 9.141 22.88 11.84 34.88 6.938C504.2 184.6 512 172.9 512 160V24C512 10.74 501.3 0 488 0z"/></svg>`;

                let url = data.url;
                let visit = '';

                if (url) {
                    visit = `<a href="${url}" class="btn visit btn-light btn-sm" target="_blank" rel="nofollow">
                        Visit Website
                        ${icon}
                    </a>`;
                }

                let casestudy = data.case_study;

                let casestudyVisit = '';

                if (casestudy) {
                    casestudyVisit = `<a href="${casestudy.permalink}" class="btn visit btn-outline-light btn-sm" target="_blank" rel="nofollow">
                        Case Study
                        ${icon}
                    </a>`;
                }

                let output = `<div class="main">
                    <div class="category">${data.category}</div>

                    <div class="title">
                        ${data.title}
                        ${visit}
                        ${casestudyVisit}
                    </div>

                    ${tags}

                    ${sliderImages}
                </div>

                <div class="scrollable">${data.desc_long ?? data.desc_short ?? ''}</div>
                `;

                slideDetailsEl.querySelector('.content').innerHTML = output;

                slideDetails.init('.details__slider .splide');

                anime({
                    targets: slideDetailsEl,
                    opacity: [0, 1],
                    scaleX: [1.5, 1],
                    scaleY: [.1, 1],
                    easing: 'easeInOutExpo',
                    duration: 1000,
                }).finished.then(() => {
                    slideDetailsEl.style.pointerEvents = 'all';
                });
            });
        });
    }
});

