if (document.querySelector('.case-studies-page')) {
    let title = 'Table of Contents';

    let anchors = document.querySelectorAll('.case-studies-page .text-wrapper h2[id]');

    let output = `<div class="gap-2 nav flex-lg-column justify-content-center justify-content-lg-start nav-pills toc-content" role="tablist" aria-orientation="vertical">`;

    let i = 0;
    anchors.forEach(anchor => {
        let text = anchor.textContent;
        let item = `<a href="#${anchor.id}" class="nav-link ${(i == 0) ? 'active' : ''}">${text}</a>`;

        output += item;

        i++;
    });

    output += `</div>`;

    document.querySelectorAll('.toc').forEach(toc => {
        toc.innerHTML = output;
    });

    let toc = document.querySelector('.toc');

    const nodeInView = (node) => {
        const io = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if(entry.isIntersecting && entry.intersectionRatio > 0.2) {
                    let target = entry.target;
                    let id = target.id;

                    // console.log(target, id);

                    let menuitem = toc.querySelector(`a[href="#${id}"]`);

                    if (toc.querySelector('a.active')) {
                        toc.querySelector('a.active').classList.remove('active');
                    }

                    menuitem.classList.add('active');
                    // observer.disconnect();
                }
            });
        },{
            root: null,
            rootMargin: '64px',
            threshold: .8,
        });

        io.observe(node);
    }

    anchors.forEach(nodeInView);
}

