if (document.querySelector('.toc-list')) {
    const nodeInView = (node) => {
        const io = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if(entry.isIntersecting && entry.intersectionRatio > 0.05) {
                    let target = entry.target;
                    let id = target.id;

                    // console.log(target, id);

                    let tocElems = document.querySelectorAll('.toc-list');

                    tocElems.forEach(toc => {
                        if (toc.querySelector('li.active')) {
                            toc.querySelector('li.active').classList.remove('active');
                        }

                        let elem = toc.querySelector(`a[href="#${id}"]`);
                        // console.log(elem);
                        // debugger;
                        elem.closest('li').classList.add('active');
                    });

                    // observer.disconnect();
                }
            });
        },{
            root: null,
            rootMargin: '0px 0px -50% 0px',
            threshold: 0.5,
        });

        io.observe(node);
    };

    let titles = document.querySelectorAll('.blog-content-wrapper h2[id], .blog-content-wrapper h3[id]');
    titles.forEach(nodeInView);
}

